@font-face {
  font-family: "Forum";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/Forum.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Rouble";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/rouble.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/GTWalsheimPro-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 500;
  src: url("fonts/GTWalsheimPro-Medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 700;
  src: url("fonts/GTWalsheimPro-Bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  object-fit: contain;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.subscribe {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #5f5f5f;
  font-weight: 400;
  text-align: center;
  margin-top: 24px;
  padding: 0 12px;
  background: #ffffff linear-gradient(180deg, #ffffff 47%, #f5f4f2 47% 100%) no-repeat top;
  background-size: cover;
}
.subscribe__container {
  background-color: #ffffff;
  border: 1px solid #E0DEDA;
  width: 100%;
  max-width: 982px;
  margin: 0 auto;
  padding: 24px 32px 23px;
}
@media (min-width: 1280px) {
  .subscribe__container {
    padding: 48px 99px;
  }
}
.subscribe__title {
  font-family: "Forum", "Georgia", serif;
  font-size: 32px;
  line-height: 35px;
  color: #474442;
  font-weight: 400;
  letter-spacing: -0.02em;
  max-width: 90%;
  margin: 0 auto 9px;
  padding: 0;
}
@media (min-width: 768px) {
  .subscribe__title {
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 8px;
  }
}
.subscribe__form {
  margin-bottom: 24px;
}
@media (min-width: 1280px) {
  .subscribe__form {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 33px;
  }
}
.subscribe__input,
.subscribe__select {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #5f5f5f;
  font-weight: 400;
  padding: 18px 26px 16px 0;
  letter-spacing: -0.02em;
  border: none;
  border-bottom: 1px solid #5f5f5f;
  width: 100%;
  transition: border-bottom 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95), color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.subscribe__input:focus,
.subscribe__select:focus {
  color: #474442;
  outline: 1px solid #474442;
}
@media (min-width: 1280px) {
  .subscribe__input,
  .subscribe__select {
    padding-bottom: 14px;
  }
}
.subscribe__description {
  margin: 0 0 15px;
  max-width: 100%;
}
@media (min-width: 1280px) {
  .subscribe__description {
    margin: 0 auto 40px;
  }
}
.subscribe__description br {
  display: none;
}
@media (min-width: 1280px) {
  .subscribe__description br {
    display: block;
  }
}
@media (min-width: 1280px) {
  .subscribe__input {
    padding-right: 40px;
  }
}
.subscribe__select {
  appearance: none;
  background-color: transparent;
  margin: 0;
  outline: none;
  cursor: pointer;
}
@media (min-width: 1280px) {
  .subscribe__select {
    padding-right: 50px;
  }
}
.subscribe__label {
  position: relative;
  display: block;
}
@media (min-width: 1280px) {
  .subscribe__label {
    margin-right: 22px;
    flex-basis: 33%;
  }
}
.subscribe__label:not(:last-of-type) {
  margin-bottom: 8px;
}
@media (min-width: 1280px) {
  .subscribe__label:not(:last-of-type) {
    margin-bottom: 0;
  }
}
.subscribe__label:last-of-type {
  margin-bottom: 32px;
}
@media (min-width: 1280px) {
  .subscribe__label:last-of-type {
    margin-bottom: 0;
  }
}
.subscribe__label--select {
  background: url("img/stack.svg#select") no-repeat top 54% right 2px;
  background-size: 14px 9px;
  cursor: pointer;
}
select::-ms-expand {
  display: none;
}
.subscribe__error {
  position: absolute;
  left: 0;
  top: calc(100% + 12px);
  text-align: left;
  color: #e07c66;
  display: none;
}
.subscribe__label.error .subscribe__input {
  color: #474442;
  border-bottom: 1px solid #e07c66;
}
.subscribe__label.error .subscribe__error {
  display: block;
}
.subscribe__input {
  line-height: 22px;
}
.subscribe__submit {
  background-color: #474442;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  font-weight: 500;
  padding: 17px 82px;
  max-width: 100%;
  transition: background-color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95), color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.subscribe__submit:hover {
  cursor: pointer;
}
@media (min-width: 1280px) {
  .subscribe__submit {
    padding: 17px 98px;
    margin-left: auto;
  }
}
.subscribe__submit:hover {
  background-color: #1A1716;
  color: #ffffff;
}
.subscribe__submit:active {
  background-color: #5f5f5f;
  color: #acacac;
}
.subscribe__agreement {
  margin: 0;
  padding: 0;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #5f5f5f;
  font-weight: 400;
}
.subscribe__agreement a {
  color: #5f5f5f;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.subscribe__agreement a:hover {
  opacity: 0.5;
}
.instagram {
  text-align: center;
  background-color: #f5f4f2;
  font-family: "Forum", "Georgia", serif;
  padding: 49px 0;
}
@media (min-width: 1280px) {
  .instagram {
    padding-top: 84px;
  }
}
.instagram__title {
  font-size: 32px;
  line-height: 35px;
  color: #474442;
  font-weight: 400;
  letter-spacing: -0.02em;
  margin: 0 0 4px -4px;
}
@media (min-width: 768px) {
  .instagram__title {
    font-size: 48px;
    line-height: 48px;
    margin: 0 auto 4px auto;
  }
}
.instagram__link {
  display: block;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  color: #5F5F5F;
  font-weight: 400;
  letter-spacing: 0.02em;
  margin: 0 0 30px -2px;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.instagram__link:hover {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .instagram__link {
    font-size: 20px;
    line-height: 24px;
    margin: 0 -8px 57px 0;
  }
}
.instagram__slider {
  height: 230px;
  margin-bottom: 24px;
}
@media (min-width: 1280px) {
  .instagram__slider {
    height: 278px;
    margin-bottom: 48px;
  }
}
.instagram__button-wrapper {
  position: relative;
  min-height: 160px;
}
@media (min-width: 768px) {
  .instagram__button-wrapper {
    min-height: 178px;
  }
}
.instagram__button-wrapper::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  height: 1px;
  background-color: #e0deda;
  width: 100%;
  z-index: 1;
}
.instagram__button {
  border: 0 transparent;
  text-decoration: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 160px;
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border-radius: 50%;
  font-family: "Forum", "Georgia", serif;
  font-size: 40px;
  line-height: 36px;
  color: #474442;
  font-weight: 400;
  letter-spacing: -0.02em;
  z-index: 2;
  transition: background-color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95), color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.instagram__button:hover {
  background-color: #474442;
  color: #ffffff;
}
.instagram__button:active {
  background-color: #5f5f5f;
  color: #acacac;
}
@media (min-width: 1280px) {
  .instagram__button {
    width: 178px;
    height: 178px;
  }
}
.instagram__button-text {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: 500;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-top: 10px;
}
.swiper-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  overflow: hidden;
}
.swiper-slide {
  flex-shrink: 0;
  margin-right: 24px;
}
.footer {
  background-color: #ffffff;
  padding: 0 20px 48px;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 15px;
  color: #474442;
  font-weight: 400;
  text-align: center;
}
@media (min-width: 1280px) {
  .footer {
    padding: 31px 41px 49px;
    background-color: #f5f4f2;
  }
}
.footer__container {
  padding: 48px 0 20px;
  background-color: #ffffff;
  max-width: 1183px;
  margin: 0 auto;
}
@media (min-width: 1280px) {
  .footer__container {
    padding: 47px 0 11px;
  }
}
.footer__top {
  padding: 0 20px 32px;
  border-bottom: 1px solid #e0deda;
}
@media (min-width: 1280px) {
  .footer__top {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    padding: 0 0 32px 44px;
    border-bottom: none;
  }
}
.footer__middle {
  padding: 31px 28px 32px;
}
.footer__middle span {
  display: block;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 15px;
  color: #474442;
  font-weight: 400;
  margin-bottom: 15px;
}
@media (min-width: 1280px) {
  .footer__middle {
    text-align: left;
    padding: 0 28px 32px 47px;
    border-bottom: 1px solid #e0deda;
  }
}
.footer__bottom {
  padding: 0 22px 0;
}
@media (min-width: 1280px) {
  .footer__bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 14px 49px 0 46px;
  }
}
.footer__bottom a {
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.footer__bottom a:hover {
  opacity: 0.5;
}
.footer__logo {
  display: block;
  margin-bottom: 30px;
  flex-shrink: 1;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.footer__logo:hover {
  opacity: 0.5;
}
@media (min-width: 1280px) {
  .footer__logo {
    margin-bottom: 0;
    margin-right: 123px;
  }
}
.footer__nav {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.footer__nav p {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #474442;
  font-weight: 700;
  margin: 0 0 9px;
}
@media (min-width: 1280px) {
  .footer__nav p {
    margin-bottom: 15px;
  }
}
.footer__nav a {
  display: block;
  text-decoration: none;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #474442;
  font-weight: 400;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.footer__nav a:hover {
  opacity: 0.5;
}
.footer__nav a:not(:last-child) {
  margin-bottom: 10px;
}
@media (min-width: 1280px) {
  .footer__nav a:not(:last-child) {
    margin-bottom: 15px;
  }
}
@media (min-width: 1280px) {
  .footer__nav {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 10px;
    row-gap: 10px;
    width: 63%;
    text-align: left;
  }
}
.footer__nav-item:not(:last-child) {
  margin-bottom: 25px;
}
@media (min-width: 1280px) {
  .footer__nav-item:not(:last-child) {
    margin-bottom: 0;
  }
}
.footer__socials {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
  max-width: 264px;
  margin: 0 auto;
}
@media (min-width: 1280px) {
  .footer__socials {
    margin: 0;
    justify-content: flex-start;
    gap: 16px;
  }
}
.footer__socials-item {
  width: 40px;
  height: 40px;
  border: 1px solid #e0deda;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-shrink: 1;
  transition: background-color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.footer__socials-item:hover {
  background-color: #474442;
}
.footer__socials-item:hover a {
  background-color: #ffffff;
}
.footer__socials-item a {
  display: block;
  width: 19px;
  height: 19px;
}
.footer__socials-item--telegram a {
  mask-image: url("./img/stack.svg#telegram");
  background-color: #474442;
}
.footer__socials-item--facebook a {
  mask-image: url("./img/stack.svg#facebook");
  background-color: #474442;
}
.footer__socials-item--instagram a {
  mask-image: url("./img/stack.svg#instagram");
  background-color: #474442;
}
.footer__socials-item--vkontakte a {
  mask-image: url("./img/stack.svg#vkontakte");
  background-color: #474442;
}
.footer__socials-item--tiktok a {
  mask-image: url("./img/stack.svg#tiktok");
  background-color: #474442;
}
.footer__rights {
  max-width: 100%;
  margin: 0 0 24px;
}
@media (min-width: 1280px) {
  .footer__rights {
    margin-bottom: 0;
  }
}
.footer__politics {
  display: block;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 15px;
  color: #474442;
  font-weight: 400;
  text-decoration: none;
  margin-bottom: 24px;
  position: relative;
}
@media (min-width: 1280px) {
  .footer__politics {
    margin-bottom: 0;
    transform: translateX(-16%);
  }
}
.footer__politics::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  height: 1px;
  width: 100%;
  background-color: #474442;
  display: none;
}
@media (min-width: 1280px) {
  .footer__politics::after {
    display: block;
  }
}
